.box {
  position: relative;
  border-radius: 3px;
  background: #ffffff;
  border-top: 3px solid #d2d6de;
  margin-bottom: 20px;
  width: 100%;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);

  &.box-primary {
    border-top-color: #0ead0b;
  }

  .box-body {
    padding: 10px;

    .pagination-wrap {
      padding-top: 10px;
      text-align: center;
    }
  }
}


.header-left {
  .small {
    display: none;
  }
}

.mini-sidebar {
  .header-left {
    .small {
      display: inline-block;
    }

    .large {
      display: none;
    }
  }
}
